<template>
  <div class="demo-switch">
    <demo-block title="基本用法">
      <gw-switch v-model="checked" />
    </demo-block>

    <demo-block title="禁用">
      <gw-switch v-model="checked5" disabled />
    </demo-block>

    <demo-block title="加载状态">
      <gw-switch v-model="checked6" loading />
    </demo-block>

    <demo-block title="自定义大小">
      <gw-switch v-model="checked2" size="24px" />
    </demo-block>

    <demo-block title="自定义颜色">
      <gw-switch
        v-model="checked3"
        active-color="#ee0a24"
        inactive-color="#dcdee0"
      />
    </demo-block>

    <demo-block title="异步控制">
      <gw-switch :model-value="checked4" @update:model-value="onUpdateValue" />
    </demo-block>
  </div>
</template>

<script>
import { reactive, toRefs } from 'vue'
import Dialog from '../../dialog'
import { Switch } from 'gw-vue-ui'

export default {
  setup() {
    const state = reactive({
      checked: true,
      checked2: true,
      checked3: true,
      checked4: true,
      checked5: true,
      checked6: true
    })

    const onUpdateValue = (checked) => {
      setTimeout(() => {
        state.checked4 = checked
      }, 500)
    }

    return {
      ...toRefs(state),
      onUpdateValue
    }
  },
  components: {
    GwSwitch: Switch
  }
}
</script>

<style lang="scss">
@import '../../styles/var';
.demo-switch {
  .gw-switch {
    margin-left: $padding-md;
  }
}
</style>
